import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue';

export default {
	data() {
		return {
			// 总数据
			Cates: [],
			// 左侧菜单数据
			leftMenuList: [],
			// 右侧商品数据
			rightContent: [],
			// 被点击的左侧菜单
			currentIndex: 0,
			// 右侧内容滚动距离顶部的距离
			scrolltop: 0
		};
	},
	components:{
		uniSearchBar
	},
	onLoad() {
		// 获取本地存储数据
		const Cates = uni.getStorageSync('cates');
		if (!Cates) {
			// 不存在数据 发送请求
			this.getCates();
		} else {
			// 有旧数据 判断是否过期
			if (Date.now() - Cates.time > 1000 * 600) {
				this.getCates();
			} else {
				// 可以使用旧数据
				this.Cates = Cates.data;
				// 构造左侧菜单数据
				this.leftMenuList = this.Cates.map(v => v.cat_name);
				// 构造右侧商品数据
				this.rightContent = this.Cates[0].children;
			}
		}
	},
	methods: {
		search() {
			uni.navigateTo({
				url: '../search/search'
			});
		},
		async getCates() {
			const res = await this.$http({
				url: '/categories'
			});
			this.Cates = res.data.message;
			// 把数据存储到本地存储中
			uni.setStorageSync('cates', {
				time: Date.now(),
				data: this.Cates
			});
			// 构造左侧菜单数据
			this.leftMenuList = this.Cates.map(v => v.cat_name);
			// 构造右侧商品数据
			this.rightContent = this.Cates[0].children;
		},
		// 左侧菜单的点击事件
		handleItemTap(i) {
			// 重新设置右侧内容的距离顶部距离
			this.scrolltop = 0;
			// 获取被点击的标题身上的索引
			// 给 currentIndex 赋值
			this.currentIndex = i;
			// 构造右侧商品数据
			this.rightContent = this.Cates[i].children;
		}
	}
};
